.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 80px;
}

.heroContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-4xl);
  align-items: center;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 var(--spacing-xl);
}

.heroText {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
}

.title {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.subtitle {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--color-text-secondary);
  margin-bottom: var(--spacing-md);
}

.description {
  max-width: 600px;
  color: var(--color-text-secondary);
  line-height: 1.8;
}

.heroVisual {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.cursorIndicator {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid var(--color-text-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cursorDot {
  width: 6px;
  height: 6px;
  background-color: var(--color-text-primary);
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

.scrollIndicator {
  position: absolute;
  bottom: var(--spacing-2xl);
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-sm);
  color: var(--color-text-secondary);
  font-size: var(--font-size-xs);
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.scrollLine {
  width: 1px;
  height: 40px;
  background-color: var(--color-text-secondary);
  animation: scrollLine 2s ease-in-out infinite;
}

@keyframes scrollLine {
  0% {
    height: 0;
    opacity: 0;
  }
  50% {
    height: 40px;
    opacity: 1;
  }
  100% {
    height: 0;
    opacity: 0;
  }
}

@media (max-width: 1024px) {
  .heroContent {
    grid-template-columns: 1fr;
    gap: var(--spacing-2xl);
    text-align: center;
  }

  .heroText {
    align-items: center;
  }

  .description {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .hero {
    padding-top: 100px;
    min-height: 90vh;
  }

  .heroContent {
    padding: 0 var(--spacing-lg);
  }
}

