.contact {
  position: relative;
}

.contactWrapper {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4xl);
}

.contactHeader {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: var(--spacing-4xl);
  align-items: flex-start;
  position: relative;
}

.contactHeaderContent {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
  max-width: 600px;
}

.contactTitle {
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-sm);
}

.sectionNumber {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text-tertiary);
  font-family: var(--font-family-mono);
}

.contactDescription {
  color: var(--color-text-secondary);
  line-height: 1.8;
}

.contactVisual {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  align-self: flex-start;
  margin-top: var(--spacing-md);
}

.cursorIndicator {
  width: 20px;
  height: 20px;
  border: 2px solid var(--color-text-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.cursorDot {
  width: 6px;
  height: 6px;
  background-color: var(--color-text-primary);
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

.contactFormSection {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: var(--spacing-4xl);
  align-items: flex-start;
}

.contactForm {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
  max-width: 600px;
}

.formActions {
  display: flex;
  justify-content: flex-start;
  margin-top: var(--spacing-sm);
}

.sendButton {
  background-color: var(--color-bg-tertiary);
  color: var(--color-text-primary);
  border: 1px solid var(--color-border);
  padding: var(--spacing-sm) var(--spacing-lg);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: var(--radius-sm);
  transition: all var(--transition-base);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
}

.sendButton:hover:not(:disabled) {
  background-color: var(--color-text-secondary);
  color: var(--color-bg-primary);
  border-color: var(--color-text-secondary);
}

.sendButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.contactFormVisual {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  align-self: flex-start;
  margin-top: calc(var(--spacing-xl) + 120px);
}

@media (max-width: 1024px) {
  .contactHeader {
    grid-template-columns: 1fr;
    gap: var(--spacing-2xl);
  }

  .contactFormSection {
    grid-template-columns: 1fr;
    gap: var(--spacing-2xl);
  }

  .contactVisual,
  .contactFormVisual {
    display: none;
  }
}

@media (max-width: 768px) {
  .contactTitle {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-md);
  }

  .contactHeaderContent,
  .contactForm {
    max-width: 100%;
  }
}

