.about {
  position: relative;
}

.aboutContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-4xl);
  align-items: center;
}

.aboutText {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
}

.aboutHeader {
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
}

.sectionNumber {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text-tertiary);
  font-family: var(--font-family-mono);
}

.aboutDescription {
  color: var(--color-text-secondary);
  line-height: 1.8;
}

.aboutDescription p {
  margin-bottom: var(--spacing-md);
}

.aboutDescription p:last-child {
  margin-bottom: 0;
}

.aboutVisual {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.aboutImage {
  width: 100%;
  max-width: 500px;
  height: 600px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: var(--radius-lg);
  position: relative;
  overflow: hidden;
}

@media (max-width: 1024px) {
  .aboutContent {
    grid-template-columns: 1fr;
    gap: var(--spacing-2xl);
  }

  .aboutVisual {
    order: -1;
  }

  .aboutImage {
    max-width: 100%;
    height: 400px;
  }
}

@media (max-width: 768px) {
  .aboutHeader {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-md);
  }
}

